import React, {Component, Fragment} from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import Icon from "antd/lib/icon"
import { connect } from 'react-redux'
import Switch from "antd/lib/switch"

class ManualSwitch extends Component {
  renderJogJoint = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      cell,
      robotStatus,
    } = this.props
    const colors = ['#AAAAAA', '#FF851B','#FF4136','#0074D9', '#01FF70']
    const names = ["","", "", "", ""]
    console.log("robot status", robotStatus)
    const color = colors[Math.floor(robotStatus)%5]
    const name = names[Math.floor(robotStatus)%5]
    const prefixCls = getPrefixCls('manual-switch', customizePrefixCls);
    return <div
      className={`${prefixCls}`}
    >
      <div>
        <Switch checkedChildren="" unCheckedChildren="" defaultChecked />
        <span></span>
      </div>
      <div>
        <Icon type="drag" />
        <span></span>
      </div>
      <div>
        <Icon type="pushpin" />
        <span></span>
      </div>
      <div>
        <Icon type="save" />
        <span></span>
      </div>
    </div>
  }

  render(){
    return <ConfigConsumer>{this.renderJogJoint}</ConfigConsumer>
  }
}

ManualSwitch.propTypes = {
  cell: PropTypes.object.isRequired,
  robotStatus: PropTypes.number.isRequired,
}

function mapStateToProps(state) {
  return {
    robotStatus: state.robot.robotStatus,
  }
}

export default connect(
  mapStateToProps,
)(ManualSwitch)

